var url = window.location.href.split("resetPassword");

var sendEvent = async function () {

  let email = document.getElementById("user").value;
  let email2 = document.getElementById("user2").value

  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: 'btn btn-success',
      cancelButton: 'btn btn-danger'
    },
    buttonsStyling: false
  });

  let title = "<strong>Verificación de Datos</strong>";
  let text = "Verifíca que el correo sea correcto";
  let icon = "error";

  function validateEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  if (email === email2 && email !== "" && validateEmail(email)) {
    window.Email.send({
      Host: "smtp.gmail.com",
      Username: "rojas.arturotsu@gmail.com",
      Password: "Artur0roja5mo9*",
      To: email,
      From: "ares-shop@gmail.com",
      Subject: "Reinicio de contraseña",
      Body: "Acceder a este link "+ url[0] + "resetPasswordApply/"+email,
    }).then(
      //message => alert(message)
    );
    text = "Correo con buen formato, revisa tu bandeja de entrada!";
    icon = "success";
  }

  await swalWithBootstrapButtons.fire({
    title: title,
    text: text,
    icon: icon
  });
};

